a{
  cursor: pointer;
}
.App {
  overflow-x: hidden;
  scroll-behavior: smooth;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: rgba(80, 80, 80, 0);
  color: white;
  background-image: url(./imgs/toronto.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.App-header {
  background-image: url(./imgs/toronto.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.banner-content{
  margin-left: calc(1vw - 2px);
  
}
.cal-month{
  text-align: center;
  height: 23px;
  width: 50px;
  border: 1px solid black;
  background-color: brown;
  color: white;
  
}
.cal-day{
  text-align: center;
  height: 23px;
  width: 50px;
  border: 1px solid black;
}
#date{
  width: 100px;
  text-align: left;
  padding-right: 10px;
}
.dates-container{
  width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.dashboard{
  width: 100%;
  background-color: black;
  height: 50px;
}
#event{
  height: 100%;
  margin: 0;
  padding: 5px 0 5px 0;
  margin-bottom: 10px;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: space-evenly;
  transition: 0.7s;
}
.event-container{
  padding-top: 10px;
  width: 100%;
}
.event-info:hover{
  /*text-decoration: underline;*/
  cursor: pointer;
}
.event-header{
  width: 800px;
  font-weight: 500;
  display: flex;
  flex-direction: row;
}
.event-info{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 700px;
  border: 1px solid #4e4e4e;
  transition: 0.7s;
  box-shadow: 1px 1px 2px 1px;
  padding-left: 20px;
}
.event-info:hover{
  transition: 0.7s;
  box-shadow: 1px 2px 10px 3px;
}
#herman-picture{
  background-image: url(./imgs/trees.jpg);
  width: 300px;
  height: 300px;
  background-position: center;
  background-size: 90%;
  border-radius: 100%;
  box-shadow: 5px 7px 15px 2px  #4e4e4e;
}
.herman-info-box{
  width: 680px;
  margin-left: 50px;
  margin-top: 35px;
}
img{
  content: url(./imgs/vector.png);
}
.intro{
  width: 1000px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-evenly;
  padding: 20px;
  transition: 0.7s;
}
#inner-banner{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  color: rgba(0, 0, 0, 0.300);
  background-color: white;
  width: 96%;
  height: 70px;
  font-size: 65px;
  font-weight: 700;
  top: 0;
}
.intro-box{
  background-color: rgba(255, 255, 255);
  height: auto;
  width: 100%;
  color: rgba(100, 100, 100);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 5px 70px 150px 2px  #4e4e4e;
  border-top: 5px solid #4e4e4e;
}
.intro-tech{
  width: 1000px;
}
.Footer{
  width: 100%;
  height: 500px;
  background-color: black;
}
#month{
  font-size: 35px;
  font-weight: 700;
}
.month-container{
  width: 900px;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-top: 40px;
}
#photo-captions{
  margin-top: 20vh;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.61);
}

.projects-container{
  width: 1600px;
  border: 1px solid red;
}

#project{
  color: black;
  font-size: 35px;
  font-weight: 700;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
#project-id{
  width: 300px;
  font-size: 25px;
  font-weight: 700;
  display: flex;
  flex-direction: row;
}
#project-num{
  color: #4e4e4e;
}
#project-title{
  width: 500px;
  font-size: 35px;
  font-weight: 700;
  padding-left: 10vw;
  text-decoration: none;
}
#project-tech{
  width: 200px;
  font-size: 25px;
  font-weight: 700;
}
.project-preview{
  width: 400px;
  height: 400px;
  border: 1px solid #4e4e4e;
}
.reflection{
  width: 680px;
  height: auto;
  animation: slide-down 1.8s ease;
}
.reflection-content{
  width: 600px;
  padding-left: 40px;
  margin-bottom: 10px;
  margin-top: 10px;
  padding-bottom: 0;
  padding-top: 0;
  animation: slide-down 1.8s ease;
}
.reflection-style-h2{
  font-size: 16px;
  font-weight: 600;
}
.reflection-style-h5{
  font-size: 15px;
  font-weight: 400;
}
.reflection-content-container{
  width: 100%;
  margin-bottom: 20px;
  margin-top: 20px;
}
.section-container{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-top: 5px solid #4e4e4e;
  border-bottom: 5px solid #4e4e4e;
}
.section-header{
  color: rgba(0, 0, 0, 0.300);
  background-color: white;
  width: 96%;
  height: 100px;
  font-size: 65px;
  font-weight: 700;
  top: 0;
}
#subtitle{
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 1vmin);
  font-weight: 700;
}
.spacer{
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 65vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 5px solid #4e4e4e;
}
.spacer-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.tech-container{
  width: 800px;
  height: auto;
  border: 1px solid black;
  display: grid;
  grid-template-columns: 100px 100px 100px 100px 100px 100px 100px 100px ;
  justify-self: center;
}
.tech-icon{
  width: 100px;
  height: 100px;
  border: 1px solid black;
}
.time-container{
  width: 1000px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  background-color: white;
  transition: 0.7s;
  margin-bottom: 12vh;
}
#time-slot{
  width: 100%;
  height: 100%;
  color: rgb(100, 100, 100);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#time-slot:hover{
  color: rgba(0, 0, 0, 0.89);
  border: 2 solid grey
}

#title{
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-size: calc(45px + 1vmin);
  font-weight: 700;
  padding-bottom: 15px;
}

.title-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  position: relative;
}
ul {
  list-style-type: square;
  list-style-position: outside;
  list-style-image: none;
}

@media (max-width: 500px) {
  .cal-month{
    height: 4vw;
    width: 8vw;
  }
  .cal-day{
    height: 4vw;
    width: 8vw;
  }
  #date{
    font-size: calc(5px + 2vmin);
  }
  #month{
    font-size: calc(25px + 1vmin);
    font-weight: 700;
  }
  .month-container{
    width: 100%;
    justify-content: center;
  }
  #subtitle{
  font-size: calc(10px + 1vmin);
  justify-content: flex-start;
  }
  #title{
    font-size: calc(25px + 1vmin);
    text-align: center;
    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
  }
  .title-card{
    height: auto;
  }
  * {
    -webkit-touch-callout:none;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
}
@media (min-width: 501px) and (max-width: 999px){
  .month-container{
    margin-left: 10vw;
  }
}
@media (max-width: 999px) {
  .App-header{
    background-position: -270px;
  }
  .about-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 3vh;
  }
  .banner-content{
    margin-left: 0;
  }
  #date{
    width: 10%;
    text-align: left;
    padding-right: 30px;
  }
  .dates-container{
    width: 80%;
  }
  .event-event{
    font-size: calc(0.7vw + 10px);
  }
  .event-header{
    width: 100%;
    font-weight: 500;
    display: flex;
    flex-direction: row;
  }
  .event-info{
    width: 100%;
    padding-right: 20px;
  }
  .event-info:hover{
    background-color: white;
  }
  #herman-picture{
    width: 250px;
    height: 250px;
    box-shadow: 2px 5px 10px 1px  #4e4e4e;
  }
  .herman-info-box{
    width: 90%;
    margin-left: 0px;
    margin-top: 0;
  }
  .intro{
    
    width: 100%;
    padding: 0.2vw, 0, 100px, 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(130, 130, 130);
    background-color: rgba(255, 255, 255, 0.91);
  }
  .intro-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .projects-container{
    width: 90%;
  }
  .section-container{
    background-image: url(./imgs/toronto.jpg);
    background-attachment: fixed;
    background-position: -270px;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .section-header{
    color: rgba(0, 0, 0, 0.300);
    background-color: rgba(255, 255, 255, 0.93);
    width: 100%;
    height: 60px;
    padding-left: 5vw;
    font-size: 45px;
    font-weight: 700;
    top: 0;
  }
  .spacer{
    background-position: -270px;
  }
  .time-container{
    width: 100%;
    background-image: url(./imgs/toronto.jpg);
    background-attachment: fixed;
    background-position: -270px;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 10vw;
  }
  #time-slot{
    background-color: rgba(255, 255, 255, 0.93);
    width: 100%;
  }
  .month-container{
    width: 100%;
  }
  .reflection{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    animation: slide-down 1.8s ease;
  }
  .reflection-content{
    width: 100%;
    height: auto;
    margin-bottom: 1vh;
    margin-top: 1vh;
    padding-left: 0px;
  }
  .reflection-style-h5{
    font-size: calc(12px + 0.1vw);
    font-weight: 400;
  }
  .reflection-style-h2{
    font-size: calc(12px + 0.1vw);
    font-weight: 600;
  }
}

@media (max-width: 1599px) {
  .projects-container{
    width: 1000px;
  }
}

